import Vue from 'vue'
import './cube-ui'
import App from './App.vue'
import router from './router'
import store from './store'

// 
import VueClipboard from 'vue-clipboard2'

Vue.use(VueClipboard)

// 
import "@/assets/style/public.scss";

// 
import publicScrip from "@/assets/script/public.js";
Vue.use(publicScrip);

// vuex
Vue.prototype.$store = store;

// 
import VueI18n from 'vue-i18n';

// 
import cn from "@/assets/lang/cn.js"; // 
import en from "@/assets/lang/en.js"; // 
import ft from "@/assets/lang/ft.js"; // 
import jp from "@/assets/lang/jp.js"; // 
import hg from "@/assets/lang/hg.js"; // 

// Vue
Vue.use(VueI18n);

// 
const langValue = localStorage.getItem("language")

// 
const i18n = new VueI18n({
	locale: langValue?langValue : 'en',  // 
	messages: {
		'cn':cn,
    'en': en,
    'ft': ft,
    'jp': jp,
    'hg': hg
	}
});

// 
Vue.prototype._i18n = i18n;


Vue.config.productionTip = false

new Vue({
  i18n,
  router,
  store,
  render: h => h(App)
}).$mount('#app')
